.Project{
    padding : 0px 10%;
   
    
}

.Project .text {
    color : white;
 
}
.blue-text{
    color : rgba(0,139,255,0.8);
}
.text h2{
    font-size: 30px;
}
.text h4{
    font-style: italic;
    font-size: 40px; 
}

.Project .List .P1{
    color : white;
}
.Project .List .P1 .Tools{
    padding: 0px 10px;
    margin-right: 10px; 
}

.Project .List .P1 .Tools a{
    padding : 0px 10px;
    align-items: center;
    border: 1px solid white;
    border-radius: 10px;
    border-color: black;
    margin-right: 10px; 
    background-color: rgba(0,139,255,0.8);
    
}

.TextDescribe{
    color : white;
    width: 250%;
    font-size: 20px;

}
.Website p{
    color : white;
    text-decoration: none;
    display: inline-block;
    padding: 10px;
    border: 2px solid white; 
    border-radius: 5px;
}

.PJ1 img{
    width : 50%;
    border: 2px solid white; 
    border-radius: 5px;
    margin-right: 50px; 
}
.PJ1 {
    display: flex;
    align-items: center;
    margin-bottom: 10%;
}


.PJ2 img{
    width : 55%;
    border: 2px solid white; 
    border-radius: 5px;
    margin-right: 50px; 
}
.PJ2 {
    display: flex;
    align-items: center;

}



.PJ2 .Left  .Website a{
    
    margin-left: 20px;
}
